.about-member-profile-div {
    position: relative;
    display: inline-block;
    width: 90%;
    height: auto;
    margin-top: 1vh;
    margin-bottom: 1vh;
    background-color: #021a2d;
    border: .2vh solid #3d4047;
    padding: 1vh;
    border-radius: 5vh;
    text-align: center;
}

.profile-pic {
    position: relative;
    width: 50%;
    height: auto;
    border: .5vh solid #505359;
    border-radius: 25vh;
    margin: 0vh;
    margin-top: 1vh;
}

.about-member-profile-div h2 {
    position: relative;
    width: 100%;
    font-size: 4vh;

    color: white;
    margin: 0vh;
    margin-top: 2vh;
}

.about-member-profile-div h3 {
    color: #0984e3;
    font-size: 3vh;
    margin: 0vh;
    margin-top: 1vh;
}

.about-member-info-div {
    position: relative;
    display: none;
    width: 90%;
    height: auto;
    border-radius: 5vh;
    background-color: #0d1019;
    border: .2vh solid #3d4047;
    margin: 2vh;
}

.about-member-role {
    display: inline-block;
    width: 90%;
    height: auto;
    color: white;
}

.about-member-bio {
    display: inline-block;
    width: 90%;
    height: auto;
    color: white;
}

.about-member-contact {
    display: inline-block;
    width: 90%;
    height: auto;
    color: white;
}

.info-header {
    display: inline-block;
    width: 33%;
    color: white;
    border-bottom: .1vh solid white;
}

.about-member-profile-div:hover .about-member-info-div {
    display: inline-block;
}

.about-member-profile-div:hover {
    animation: show_about 2s;
    cursor: pointer;
}

.about-member-profile-div:hover {
    animation: show_about 2s;
}

.about-member-profile-div:hover .profile-pic {
    animation: transition_img 2s;
    width: 10%;
}



@keyframes transition_img {
    0% {
        width: 25%;
	}

    100% {
        width: 10%;
	}
}


@keyframes show_about {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);
        border: .1vh white;
	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}