@import url('https://fonts.googleapis.com/css?family=Audiowide');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Audiowide';
    overflow-y: scroll;

}

.about-us {
    width: 100%;
    height: 100%;
}

.welcome-div {
    display: inline-block;
    text-align: center;
    width: 100%;
    height: auto;
    margin-bottom: 5vh;
}

.welcome-div-header
{
    position: relative;
    margin: 2.5%;
    display: inline-block;
    width: 66%;
    height: auto;

}

.welcome-div-header h1
{
    font-size: 7vh;
    font-weight: 100;
    color: white;
    border-bottom: .5vh solid white;
}

.welcome-div-header h3
{
    font-size: 3.5vh;
    font-style: italic;
    color: #0984e3;
}

.welcome-div-body
{
    display: inline-block;
    width: 100%;
    height: auto;
}

.our-story {
    width: 100%;
    display: inline-block;
    justify-content: center;
}

.mission-div {
    display: inline-block;
    text-align: center;
    width: auto;
    height: auto;
}

.mission-div-header {
    width: auto;
}

.mission-div-subheader {
    width: 100%;
}

.mission-div-header h3 {
    width: auto;
    font-size: 4vh;
    color: #0984e3;
    margin-bottom: 1vh;
    font-weight: 500;
}

.mission-div h4 {
    font-size: 3vh;
    color: white;
    margin: 0;
    padding: 0;
    font-weight: 250;
    margin-bottom: 5vh;
    padding-bottom: .5vh;
    border-bottom: .1vh solid grey;
}

.story-div {
    position: relative;
    display: inline-flex;
    justify-content: center;
    width: 100%;
    height: auto;
}


.what-we-are {
    margin-left: 50%;
    width: 40%;
    height: auto;
}

.story-header {
    width: 100%;
    height: auto;
    margin: 0;
    padding: 0;
}

.story-header h3 {
    width: auto;
    font-size: 4vh;
    color: #0984e3;
    margin-bottom: 1vh;
    font-weight: 100;
    margin-top: 5vh;
}

.story-body {
    width: 100%;
    height: auto;
    padding: 0;

}

.story-body p{
    font-size: 3vh;
    color: white;
    padding: 2.5vh;
    margin: 0;
    padding: 5vh;
    border: .1vh solid grey;
    height: auto;
}

.our-start-div {
    width: 66%;
    height: auto;
}

.our-past {
    position: relative;
    display: inline-block;
    margin: 12vh;
    width: 33%;
    height: 50vh;
    background-color: green; 
}

.sub-story {
    display: inline-block;
    margin: 12vh;
    width: 33%;
    height: auto;
}

.meet-the-team {
    width: 100%;
    margin: 0;
    padding: 0;
    height: auto;
}

.meet-team-header {
    display: inline-flex;
    justify-content: center;
    height: auto;
    width: 100%;
}

.meet-team-header h3 {
    font-size: 5vh;
    font-weight: 900;
    color: #0984e3;
    border-bottom: .25vh solid white;
}

.meet-the-team-div {
    display: inline-flex;
    justify-content: space-around;
    width: 100%;
    height: auto;

}

.meet-the-team-left-div {
    width: 33%;
    height: auto;
    display: inline-block;
    text-align: center;
}

.meet-the-team-right-div {
    width: 33%;
    height: auto;
    display: inline-block;
    text-align: center;
}

.container {
    width: 100%;
    display: inline-flex;
}

.player-1 {
    position: absolute;
    width: 100%;
    top: 100%;
    z-index: -1;
    opacity: .33;
    justify-content: left;
}

.player-2 {
    position: absolute;
    top: 10vh;
    left: 0;
    width: 66%;
    z-index: -2;
    opacity: .33;
}

@media (max-width: 1000px) 
{

    .sub-story {
        width: 90%;
        margin: 1vh;
    }

    .story-div {
        display: inline-block;
        padding: 0vh;
        padding-left: -1vh;
        
    } 

    .story-body p{
        padding: 1vh;
    } 

    .welcome-div-header {
        padding: 1vh;
        width: 90%;
        margin: 0;
    }

    .what-we-are {

        width: 90%;
        margin: auto;

    }

    .our-start-div {

        margin: auto;
        padding: 0vh;
        width: 90%;
    } 

    .meet-the-team-div {
        display: inline-block;
        width: 100%;
    }

    .meet-the-team-left-div {
        width: 100%;
    }

    .meet-the-team-right-div {
        width: 100%;
    }
}